  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-display: swap;
  font-family: 'PFDinTextUniversal';
  src: url("../fonts/PFDinTextUniversal/PFDinTextUniversal.woff2") format("woff2"), url("../fonts/PFDinTextUniversal/PFDinTextUniversal.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-display: swap;
