/* GLOBALS */
*::-webkit-input-placeholder {
  color: #b8b8b8 !important;
  opacity: 1 !important;
}

*:-moz-placeholder {
  color: #b8b8b8 !important;
  opacity: 1 !important;
}

*::-moz-placeholder {
  color: #b8b8b8 !important;
  opacity: 1 !important;
}

*:-ms-input-placeholder {
  color: #b8b8b8 !important;
  opacity: 1 !important;
}

html * {
  max-height: 1000000px;
}

html {
  font-size: 15px;
}

html, body {
  min-height: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch !important;
}

html[dir="rtl"] {
  direction: rtl;
}

html[dir="rtl"] *:not(button) {
  text-align: right;
}

html[dir="rtl"] input[type=email],
html[dir="rtl"] input[type=tel],
html[dir="rtl"] input[type=number],
html[dir="rtl"] [data-input-number],
html[dir="rtl"] [data-input-number-with-string-placeholder] {
  text-align: left;
  direction: ltr;
}

html[dir="rtl"] [data-input-number-with-string-placeholder]::placeholder {
  text-align: right;
}

body {
  position: relative;
  min-width: 320px;
  font-size: 1rem;
  font-family: "PFDinTextUniversal", sans-serif;
  line-height: 1.467;
  background: #f2f2f2;
  color: #282a2e;
}

html[dir="rtl"] body {
  font-family: 'Cairo', sans-serif;
}

body.locked {
  position: relative;
  overflow: hidden;
  min-height: 0;
  height: 100%;
}

body.locked .wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
}

body.locked .footer {
  position: static;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

@media (min-width: 1480px) {
  .container {
    max-width: 1450px;
  }
}

.wrapper {
  position: relative;
  min-height: 100%;
  height: auto;
}

input:not([type=checkbox]):not([type=radio]):not([type=number]), textarea, select {
  -webkit-appearance: none !important;
}

a {
  color: #124eb3;
  text-decoration: none;
}

a:hover, a:focus {
  color: rgba(18, 78, 179, 0.75);
  text-decoration: underline;
}

a:active {
  color: rgba(18, 78, 179, 0.5);
}

iframe {
  border: none;
}

p {
  margin-bottom: 20px;
}

/* Headings */
.block-title {
  position: relative;
  margin-bottom: 0;
  margin-right: 20px;
  padding-bottom: 10px;
  font-size: 1.33333rem;
  font-weight: 500;
  line-height: 1.1;
}

html[dir="rtl"] .block-title {
  margin-right: 0;
  margin-left: 20px;
}

@media (min-width: 768px) {
  .block-title {
    font-size: 2.13333rem;
  }
}

.block-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 70px;
  height: 4px;
  background: #ffa502;
}

html[dir="rtl"] .block-title::after {
  left: auto;
  right: 0;
}

.page-title {
  margin-bottom: 0;
  font-size: 1.33333rem;
  font-weight: 700;
  color: #0d4094;
}

