select.custom-select + .select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 10px;
}

html[dir="rtl"] select.custom-select + .select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 40px;
}

select.custom-select + .select2-container.select2-container--default .select2-selection--single .select2-selection__rendered {
  padding-right: 40px;
  line-height: 50px;
  color: #282a2e;
}

html[dir="rtl"] select.custom-select + .select2-container.select2-container--default .select2-selection--single .select2-selection__rendered {
  padding-right: 10px;
}

select.custom-select + .select2-container.select2-container--default .select2-selection--single .select2-selection__arrow {
  height: 48px;
  width: 40px;
}

select.custom-select + .select2-container.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border: none;
  background-image: url(data:image/svg+xml;base64,);
  width: 8px;
  height: 6px;
