// Disable android font boosting
html * {
  max-height: 1000000px; }

html {
  font-size: $baseFontSize; }

html, body {
  // For gradient background
  min-height: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch !important;
 }  // scroll-behavior: smooth

html[dir="rtl"] {
  direction: rtl;
  * {
    &:not(button) {
      text-align: right; } }
  input[type=email],
  input[type=tel],
  input[type=number],
  [data-input-number],
  [data-input-number-with-string-placeholder] {
    text-align: left;
    direction: ltr; }
  [data-input-number-with-string-placeholder] {
    &::placeholder {
      text-align: right; } } }

body {
  position: relative;
  min-width: 320px;
  font-size: 1rem;
  font-family: $defaultFontFamily;
  line-height: $baseLineHeight;
  background: $bgColor;
  color: $textColor;
  @include rtl {
    font-family: 'Cairo', sans-serif; } }

// Лочу скролл в модалках, меню и т.д.
body.locked {
  position: relative;
  overflow: hidden;
  min-height: 0;
  height: 100%;
  .wrapper {
    position: relative;
    overflow: hidden;
    height: 100%; }
  // Убираю sticky footer чтобы не мешался
  .footer {
    position: static; } }

.container {
  @media (min-width: 1200px) {
    max-width: 1170px; }
  @media (min-width: 1480px) {
    max-width: 1450px; } }

.wrapper {
  position: relative;
  // For Magnific Popup scrollbar problem ( Better to use 100% with html, body {height 100%} )
  // min-height: 100vh
  min-height: 100%;
  height: auto;

 }  // overflow-x: hidden

input:not([type=checkbox]):not([type=radio]):not([type=number]), textarea, select {
  -webkit-appearance: none !important; // Remove inputs and textareas inner shadow on Mobile Safari (iPhone)
 }  // border-radius: 0 !important // Если бордюры не нужны, нужно сбросить иначе на ios будут по дефолту

