      @include rtl {
        margin-right: 0;
        margin-left: 15px; } } } }

.comment__top-right-side {
  display: flex;
  align-items: center;
  margin-left: auto;
  @include rtl {
    margin-left: 0;
    margin-right: auto; } }

.comment__name {
  position: relative;
  padding-left: 23px;
  font-size: $fz14px;
  line-height: 1.1;
  font-weight: 700;
  @include rtl {
    padding-left: 0;
    padding-right: 23px; }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @include rtl {
      left: auto;
      right: 0; } } }

.comment__name--no-icon {
  padding-left: 0;
  @include rtl {
    padding-right: 0; }
  &::after {
    display: none; } }

.comment__name--accent {
  color: $accent;
  &::after {
    background-image: url(data:image/svg+xml;base64,); } }

.comment__attached-link {
  display: block;
  width: 15px;
  height: 15px;
  margin-left: 15px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  opacity: 0.3;
  @include rtl {
    margin-left: 0;
    margin-right: 15px; }
  &:hover, &:focus {
    opacity: 1; }
  @media (min-width: 1200px) {
    position: absolute;
    top: 50%;
    right: -25px;
    margin-top: -12.5px;
    @include rtl {
      right: auto;
      left: -25px; } } }

.comment__text {
  margin-bottom: 10px;
  font-size: $fz14px;
  line-height: 1.43; }

.comment__bottom {
  display: flex;
  > * {
    &:not(:last-child) {
      margin-right: 10px;
      @include rtl {
        margin-right: 0;
        margin-left: 10px; } } } }

.comment__reply {
  position: relative;
  font-size: $fz14px;
  font-weight: 400;
  padding-left: 23px;
  background: none;
  border: none;
  @include rtl {
    padding-left: 0;
    padding-right: 23px; }
  &:focus {
    outline: none; }
  &:hover, &:focus {
    text-decoration: none;
    opacity: 0.75; }
  &:active {
    opacity: 0.5; }
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 15px;
    height: 15px;
    margin-top: -9.5px;
    background-image: url(data:image/svg+xml;base64,);
    background-size: cover;
    @include rtl {
      left: auto;
      right: 0;
      transform: scaleX(-1); } } }

// .comment__date

.comments-user-section {
  margin-bottom: 10px;
  a {
    text-decoration: underline; } }

.comments-user-section__no-comments-text {
  margin-bottom: 15px; }

.write-a-comment {
  display: none;
  &.active {
    display: block; } }

.write-a-comment, .write-a-message {
  position: relative;
  textarea {
    width: 100%;
    min-height: 160px;
    padding: 15px 55px 15px 15px;
    font-size: $fz16px;
    background-color: white;
    border: 1px solid $borderColor;
    box-shadow: $cardShadow;
    border-radius: 0;
    @include rtl {
      padding: 15px 15px 15px 55px; }
    &:focus {
      outline: none;
      border-color: $borderColorFocus; } }
  > button {
    position: absolute;
    top: 15px;
    right: 25px;
    padding: 0;
    background: none;
    border: none;
    @include rtl {
      right: auto;
      left: 25px;
      transform: scaleX(-1); }
    path {
      fill: #b1b1b1; }
    &:focus {
      outline: none; }
    &:hover, &:focus {
      path {
        fill: $accent2; } } } }

.write-a-comment__btn {
  display: flex;
  flex-direction: row-reverse; }


/* Star rating */

.set-rating {
  display: inline-flex;
  flex-direction: row-reverse;
  input {
    position: absolute;
    display: block;
    height: 30px;
    width: 1px;
    opacity: 0;
    &:checked ~ label {
      background-image: url(../img/star-active.svg); } }
  &:hover {
    label {
      background-image: url(../img/star.svg) !important; } }
  label {
    margin: 0;
    width: 20px;
    height: 20px;
    background-image: url(../img/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    &:not(:first-of-type) {
      width: 25px;
      border-right: 5px solid transparent;
      @include rtl {
        border-right: none;
        border-left: 5px solid transparent; } }
    &:hover {
      background-image: url(../img/star-active.svg) !important;
      & ~ label {
        background-image: url(../img/star-active.svg) !important; } } } }


.star-rating {
  position: relative;
  z-index: 1;
  display: flex;
  width: 62px;
  height: 12px;
  background-image: url(../img/star.svg);
  background-size: auto 12px;
  transform: scaleX(-1); }

.star-rating__inner {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background-image: url(../img/star-active.svg);
  background-size: auto 12px; }


.load-more-btn-container {
  display: flex;
  justify-content: center; }

.load-more-btn {
  display: flex;
  align-items: center;
  padding: 0;
  font-size: $fz14px;
  text-transform: uppercase;
  background: none;
  border: none;
  color: $accent2;
  &:focus {
    outline: none; }
  &:hover, &:focus {
    text-decoration: none;
    opacity: 0.75; }
  &:active {
    opacity: 0.5; }
  &::before {
    content: '';
    display: block;
    width: 25px;
    height: 25px;
    margin-right: 5px;
    background-image: url(data:image/svg+xml;base64,);
    background-size: cover;
    @include rtl {
      margin-right: 0;
      margin-left: 5px; } } }


.trash-btn {
  display: block;
  width: 21px;
  height: 21px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  &:focus {
    outline: none; }
  &:hover, &:focus {
    opacity: 0.75; }
  &:active {
    opacity: 0.5; } }

.message-btn {
  position: relative;
  display: block;
  width: 23px;
  height: 23px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  &:focus {
    outline: none; }
  &:hover, &:focus {
    opacity: 0.75; }
  &:active {
    opacity: 0.5; }
  > span {
    position: absolute;
    top: -50%;
    right: -50%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    font-size: $fz12px;
    line-height: 1;
    color: $textColor3;
    background: #f24841;
    border-radius: 50%; } }


.attach-file {
  input {
    display: none; }
  label {
    position: relative;
    padding-left: 18px;
    font-size: $fz14px;
    line-height: 1.2;
    cursor: pointer;
    @include rtl {
      padding-left: 0;
      padding-right: 18px; }
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      margin-top: -8px;
      left: 0;
      width: 13px;
      height: 15px;
      background-image: url(data:image/svg+xml;base64,);
      background-size: cover;
      @include rtl {
        left: auto;
        right: 0;
        transform: scaleX(-1); } }
    &:focus {
      outline: none; }
    &:hover, &:focus {
      color: $accent; } } }


.edit-btn {
  display: flex;
  align-items: center;
  padding: 0;
  font-size: $fz13px;
  line-height: 1;
  text-transform: uppercase;
  background: none;
  border: none;
  color: $accent2;
  &:focus {
    outline: none; }
  &:hover, &:focus {
    text-decoration: none;
    opacity: 0.75; }
  &:active {
    opacity: 0.5; }
  &::before {
    content: '';
    display: block;
    width: 16px;
    height: 14px;
    margin-right: 5px;
    background-image: url(data:image/svg+xml;base64,);
    background-size: cover;
    @include rtl {
      margin-right: 0;
      margin-left: 5px; } } }


/* To top button */

.to-top-button {
  position: fixed;
  display: block;
  visibility: hidden;
  opacity: 0;
  z-index: 99;
  right: 20px;
  bottom: 15px;
  width: 43px;
  height: 43px;
  border: none;
  // border-radius: 5px
  background-color: transparent;
  background-image: url(data:image/svg+xml;base64,);
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid $accent3;
  transition: all 0.3s;
  &:focus {
    outline: none; }
  &.fixed {
    visibility: visible;
    opacity: 1; }
  @media (min-width: 768px) {
    bottom: 160px; }
  @media (min-width: 1200px) {
    right: 60px;
    bottom: 160px; } }
