/* Header */

.header {}

.header-desktop {
  display: none;
  @media (min-width: 992px) {
    display: block;
    background: $accent2; } }

.header__button {
  position: relative;
  @media (max-width: 991px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    > a {
      padding: 14px 0;
      align-items: center; } }
  @media (min-width: 992px) {
    padding: 20px 0; }
  &:not(:last-child) {
    margin-right: 60px;
    @include rtl {
      margin-right: 0;
      margin-left: 60px; } }
  > a {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: $fz14px;
    line-height: 1;
    color: inherit;
    &:hover, &:focus {
      text-decoration: none;
      color: inherit;
      text-shadow: 0 0 1px $textColor3; }
    &::before {
      content: '';
      display: block;
      min-width: 20px;
      max-width: 20px;
      height: 20px;
      margin-right: 10px;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      @include rtl {
        margin-right: 0;
        margin-left: 10px; } } } }

.header__button--text-overflow {
  span {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    @media (max-width: 425px) {
      max-width: 45px; }
    @media (max-width: 360px) {
      max-width: 35px; } } }

.header__button--dropdown {
  @media (max-width: 991px) {
    > a {
      span {
        position: relative;
        padding-right: 13px;
        @include rtl {
          padding-right: 0;
          padding-left: 13px; }
        &::after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          width: 8px;
          height: 6px;
          margin-top: -2px;
          background-image: url(data:image/svg+xml;base64,);
          background-position: center;
          background-repeat: no-repeat;
          @include rtl {
            right: auto;
            left: 0; } } }
      &.active {
        span {
          &::after {
            transform: scaleY(-1); } } } } }
  @media (min-width: 992px) {
    > a {
      padding-right: 13px;
      @include rtl {
        padding-right: 0;
        padding-left: 13px; }
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 8px;
        height: 6px;
        margin-top: -2px;
        background-image: url(data:image/svg+xml;base64,);
        background-position: center;
        background-repeat: no-repeat;
        @include rtl {
          right: auto;
          left: 0; } }
      &.active {
        &::after {
          transform: scaleY(-1); } } } } }

.header__button--lang {
  > a {
    &::before {
      background-image: url(data:image/svg+xml;base64,); } } }

.header__button--currency {
  > a {
    &::before {
      background-image: url(data:image/svg+xml;base64,); } } }

.header__button--login {
  > a {
    &::before {
      background-image: url(data:image/svg+xml;base64,); } } }

.header__dropdown {
  display: none;
  position: absolute;
  top: calc(100% + 7px);
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
  min-width: 85px;
  background-color: $white;
  box-shadow: 0 3px 7.2px 0.8px rgba(0, 0, 0, 0.15);
  @media (min-width: 992px) {
    top: 100%; }
  &::before {
    content: '';
    position: absolute;
    top: -7px;
    left: 50%;
    margin-left: -7px;
    width: 14px;
    height: 14px;
    background: $white;
    transform: rotate(45deg); }
  ul {
    margin: 0;
    padding: 12px 0;
    list-style-type: none;
    li {
      a {
        display: block;
        padding: 6px 25px;
        line-height: 1.1;
        color: $textColor;
        text-align: center !important;
        &:hover, &:focus {
          text-decoration: none;
          background-color: $accent2;
          color: $textColor3; } } } } }

@media (min-width: 992px) {
  // .header-desktop__top
  //// padding: 12px 0

  .header-desktop__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  .header-desktop__bar {
    display: flex;
    align-items: center;
    font-size: $fz14px;
    color: $textColor3; }

  .header-desktop__bottom {
    background: $accent; }

  .header-desktop__menu {
    position: relative;
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none;
    > li {
      flex: 1;
      display: flex;
      border-left: 1px solid darken($accent, 5%);
      &:last-child {
        border-right: 1px solid darken($accent, 5%); }
      > a {
        position: relative;
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 18.5px 15px;
        font-weight: 500;
        font-size: $fz13px;
        line-height: 1;
        text-transform: uppercase;
        color: $textColor3;
        &::before {
          display: none;
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          left: 0;
          height: 4px;
          background: $accent4; }
        &:hover, &:focus {
          text-decoration: none; }
        &:active {
          background: #3b64a7;
          &::before {
            display: block;
            background: #d89823; } }
        &:hover, &.active {
          &::before {
            display: block; } } } } }

  .header-desktop__submenu-link {
    position: relative;
    span {
      position: relative;
      padding-right: 13px;
      @include rtl {
        padding-right: 0;
        padding-left: 13px; }
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        width: 8px;
        height: 6px;
        margin-top: -2px;
        background-image: url(data:image/svg+xml;base64,);
        background-position: center;
        background-repeat: no-repeat;
        @include rtl {
          right: auto;
          left: 0; } } }
    &.active {
      span {
        &::after {
          transform: scaleY(-1); } }
      & ~ .header-desktop__submenu {
        display: block; } } }

  .header-desktop__submenu {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    z-index: 15;
    background: $white;
    border: 1px solid $borderColor;
    box-shadow: 0 3px 7.2px 0.8px rgba(0, 0, 0, 0.03);
    @media (min-width: 1480px) {
      right: 10%;
      left: 10%; }
    > ul {
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      padding: 10px 0 30px;
      list-style-type: none;
      @media (min-width: 1200px) {
        padding: 10px 50px 30px; }
      > li {
        flex-basis: 20%;
        display: flex;
        > a {
          display: flex;
          align-items: center;
          width: 100%;
          padding: 20px;
          color: $textColor;
          &:hover, &:focus {
            text-decoration: none;
            box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2); }
          > span {
            &:nth-child(1) {
              width: 50px;
              margin-right: 15px;
              @include rtl {
                margin-right: 0;
                margin-left: 15px; } }
            &:nth-child(2) {
              > span {
                display: block;
                line-height: 1.1;
                &:nth-child(2) {
                  font-size: $fz14px;
                  color: $textColor2; } } } } } } } }

  .header-desktop__menu-search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 70px 0;
    label {
      margin: 0;
      padding-right: 15px;
      font-size: $fz16px;
      line-height: 1;
      @include rtl {
        padding-right: 0;
        padding-left: 15px; } }
    > div {
      position: relative;
      width: 40%;
      input {
        width: 100%;
        height: 45px;
        padding: 0 15px 0 50px;
        font-size: $fz14px;
        background: $white;
        border: 1px solid $borderColor;
        @include rtl {
          padding: 0 50px 0 15px; }
        &:focus {
          outline: none;
          border-color: #e0ebfd; } }
      svg {
        position: absolute;
        top: 12px;
        left: 16px;
        background: none;
        border: none;
        width: 20px;
        height: 20px;
        @include rtl {
          left: auto;
          right: 16px; }
        path {
          fill: $accent2; } } } } }


.header-mobile {
  display: none;
  @media (max-width: 991px) {
    display: block; } }

@media (max-width: 991px) {

  .header-mobile__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background: $accent2; }

  .header-mobile__logo {
    // width: 45px
    height: 26px;
    img {
      max-width: 100%;
      max-height: 100%; } }

  .header-mobile__bar {
    display: flex;
    align-items: center; }

  .header-mobile__menu-btn {
    width: 25px;
    display: block;
    margin-left: 20px;
    padding: 0;
    background: none;
    border: none;
    cursor: pointer;
    @include rtl {
      margin-left: 0;
      margin-right: 20px; }
    &:focus {
      outline: none; }
    span {
      width: 25px;
      height: 2px;
      background-color: $white;
      display: block;
      margin: 6px auto;
      transition: all $mainAnimationSpeed; }
    &.active {
      span {
        &:nth-child(2) {
          opacity: 0; }
        &:nth-child(1) {
          transform: translateY(8px) rotate(45deg); }
        &:nth-child(3) {
          transform: translateY(-8px) rotate(-45deg); } } } }

  .header-mobile__menu {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: $bgColor;
    overscroll-behavior-y: contain;
    overflow-y: auto;
    &.active {
      display: block; } }

  .header-mobile__menu-submenu {
    display: none;
    flex-wrap: wrap;
    margin: 0;
    padding: 10px 15px;
    list-style-type: none;
    li {
      margin-bottom: 15px;
      display: flex;
      flex-basis: 100%;
      @media (min-width: 360px) {
        flex-basis: 50%; }
      @media (min-width: 576px) {
        flex-basis: 33.33333333%; }
      @media (min-width: 768px) {
        flex-basis: 25%; }
      a {
        display: flex;
        align-items: center;
        padding: 7px 10px 7px 0;
        color: $textColor;
        @include rtl {
          padding: 7px 0 7px 10px; }
        &:hover, &:focus {
          text-decoration: none; }
        > span {
          &:nth-child(1) {
            width: 50px;
            margin-right: 15px;
            @include rtl {
              margin-right: 0;
              margin-left: 15px; } }
          &:nth-child(2) {
            > span {
              display: block;
              line-height: 1.1;
              &:nth-child(2) {
                font-size: $fz14px;
                color: $textColor2; } } } } } } }

  .header-mobile__menu-inner {
    > ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
      background: $white;
      border-top: 3px solid $borderColor;
      > li {
        border-bottom: 1px solid $borderColor;
        > a, > span {
          display: block;
          padding: 19px 15px;
          font-weight: 700;
          font-size: $fz13px;
          text-transform: uppercase;
          color: $textColor;
          &:hover, &:focus {
            text-decoration: none; } } } } }

  .header-mobile__menu-submenu-link {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 15px;
      width: 8px;
      height: 6px;
      margin-top: -3px;
      background-image: url(data:image/svg+xml;base64,);
      background-position: center;
      background-repeat: no-repeat;
      @include rtl {
        right: auto;
        left: 15px; } }
    &.active {
      &::after {
        transform: scaleY(-1); }
      + .header-mobile__menu-submenu {
        display: flex; } } }

  .header-mobile__menu-btns {
    position: relative;
    display: flex;
    padding: 0 15px 7px;
    background-color: $accent2;
    color: $textColor3;
    a {
      &:hover, &:focus {
        text-decoration: none; } }
    > div {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: stretch;
      padding: 0 10px;
      &:not(:last-child) {
        border-right: 1px solid #3069c9; }
      &:last-child {
        position: relative; }
      > button, > a {
        display: block;
        width: 100%;
        height: 100%;
        padding: 22.5px 0;
        text-transform: uppercase;
        text-align: center;
        background: none;
        border: none;
        &:focus {
          outline: none; } } } } }
