/* Main page */

.hero {
  position: relative; }

.hero__slider {
  background: $emptyColor;
  img {
    height: 552px;
    object-fit: cover;
    font-family: 'object-fit: cover;';
    @media (min-width: 768px) {
      height: 638px; } } }

$heroSearchBg: rgba(black, 0.6);
.hero__search {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1;
  padding: 155px 0;
  color: $textColor3;
  @media (max-width: 991px) {
    padding: 25px 0 0; } }

.hero-tabs {
  margin: 0 auto;
  max-width: 1165px;
  @media (min-width: 992px) {
    padding: 0 20px; } }

.hero-tabs__btns {
  display: flex;
  @media (max-width: 991px) {
    padding: 0 15px; }
  button {
    margin-right: 1px;
    padding: 8px 20px;
    font-weight: 700;
    text-transform: uppercase;
    color: rgba($textColor3, 0.7);
    background: $heroSearchBg;
    border: none;
    border-bottom: 4px solid #644918;
    &.active {
      color: $textColor3;
      border-color: $accent4; }
    &:focus {
      outline: none; }
    @media (max-width: 991px) {
      padding: 8px 15px;
      font-size: $fz12px; }
    @media (max-width: 359px) {
      padding: 8px 10px;
      font-size: $fz11px; } } }

.hero-tabs__content {
  position: relative;
  background: $heroSearchBg;

  @media (max-width: 991px) {
    padding: 35px 15px 29px; }

  @media (min-width: 992px) {
    padding: 40px 25px;
    &::before {
      content: '';
      position: absolute;
      top: -2px;
      right: -2px;
      bottom: -2px;
      left: -2px;
      border: 1px solid $heroSearchBg;
      z-index: -1; } }

  > div {
    display: none;
    &.active {
      display: block; }

    &:nth-child(1) {
      @media (max-width: 991px) {
        > form {
          > div {
            &:nth-child(1) {
              > div {
                > div {
                  margin-bottom: 15px; } } }
            &:nth-child(2) {
              display: flex;
              flex-direction: column;
              > div {
                // &:nth-child(1)
                &:nth-child(2) {
                  order: -1;
                  margin: 5px 0 20px; } } } } } }

      @media (min-width: 992px) {
        > form {
          display: flex;

          > div {
            &:nth-child(1) {
              width: 73.5%;
              margin-right: 1.8%;
              @include rtl {
                margin-right: 0;
                margin-left: 1.8%; }

              > div {
                &:nth-child(1) {
                  display: flex;
                  margin-bottom: 30px;
                  > div {
                    &:nth-child(1) {
                      width: 69.5%;
                      margin-right: 2.5%;
                      @include rtl {
                        margin-right: 0;
                        margin-left: 2.5%; } }
                    &:nth-child(2) {
                      width: 28%; } } }
                &:nth-child(2) {
                  position: relative;
                  display: flex;
                  > div {
                    &:nth-child(1) {
                      width: 33.5%;
                      margin-right: 2.5%;
                      @include rtl {
                        margin-right: 0;
                        margin-left: 2.5%; } }
                    &:nth-child(2) {
                      width: 33.5%;
                      margin-right: 2.5%;
                      @include rtl {
                        margin-right: 0;
                        margin-left: 2.5%; } }
                    &:nth-child(3) {
                      width: 28%; } } } } }

            &:nth-child(2) {
              width: 24.7%;
              > div {
                &:nth-child(1) {
                  margin-bottom: 30px; }
                &:nth-child(2) {
                  display: flex;
                  align-items: center;
                  height: 50px; } } } } } } }

    &:nth-child(2) {
      @media (max-width: 991px) {
        > form {
          > div {
            &:nth-child(1) {
              > div {
                margin-bottom: 15px;
                &:nth-child(3) {
                  margin-bottom: 20px; } } } } } }

      @media (min-width: 992px) {
        > form {
          display: flex;

          > div {
            &:nth-child(1) {
              display: flex;
              width: 73.5%;
              margin-right: 1.8%;
              @include rtl {
                margin-right: 0;
                margin-left: 1.8%; }
              > div {
                &:nth-child(1) {
                  width: 54.5%;
                  margin-right: 2.5%;
                  @include rtl {
                    margin-right: 0;
                    margin-left: 2.5%; } }
                &:nth-child(2) {
                  width: 22.5%;
                  margin-right: 2.5%;
                  @include rtl {
                    margin-right: 0;
                    margin-left: 2.5%; } }
                &:nth-child(3) {
                  width: 18%; } } }


            &:nth-child(2) {
              width: 24.7%; } } } } } } }


.main-page-title {
  position: relative;
  margin-bottom: 8px;
  font-size: $fz20px;
  line-height: 1.1;
  font-weight: 700;
  h1, h2, h3 {
    margin-bottom: 0; }
  @media (min-width: 768px) {
    font-size: $fz32px;
    &::before, &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      height: 1px;
      background: $accent4; }
    &::before {
      margin-top: -3px; }
    &::after {
      margin-top: 5px; } } }


.main-page-title__container {
  text-align: center;
  > div {
    position: relative;
    z-index: 1;
    display: inline-block;
    > * {
      font-size: inherit; }
    @media (min-width: 768px) {
      padding: 0 35px;
      background: $bgColor;
 } } }      // background: red


// .car-block

.car-block__top {
  display: flex;
  margin-bottom: 40px;
  @media (max-width: 767px) {
    flex-direction: column;
    .car-block__link {
      margin-top: 10px; } }
  @media (min-width: 768px) {
    flex-wrap: wrap;
    align-items: center; } }

.car-block__link {
  margin-left: auto;
  font-size: $fz14px;
  @include rtl {
    margin-left: 0;
    margin-right: auto; } }

.car-cards {
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 767px) {
    justify-content: center; }
  > * {
    width: 100%;
    @media (min-width: 768px) and (max-width: 991px) {
      width: 48.55%;
      margin-right: 2.9%;
      @include rtl {
        margin-right: 0;
        margin-left: 2.9%; }
      &:nth-child(2n+0) {
        margin-right: 0;
        @include rtl {
          margin-left: 0; } } }
    @media (min-width: 992px) and (max-width: 1199px) {
      width: 31.786666666666665%;
      margin-right: 2.32%;
      @include rtl {
        margin-right: 0;
        margin-left: 2.32%; }
      &:nth-child(3n+0) {
        margin-right: 0;
        @include rtl {
          margin-left: 0; } } }
    @media (min-width: 1200px) {
      width: 23.9436625%;
      margin-right: 1.40845%;
      @include rtl {
        margin-right: 0;
        margin-left: 1.40845%; }
      &:nth-child(4n+0) {
        margin-right: 0;
        @include rtl {
          margin-left: 0; } } } } }

.car-small-cards {
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 767px) {
    justify-content: center; }
  > * {
    width: 100%;
    @media (min-width: 768px) and (max-width: 1199px) {
      width: 48.55%;
      margin-right: 2.9%;
      &:nth-child(2n+0) {
        margin-right: 0; }
      @include rtl {
        margin-right: 0;
        margin-left: 2.9%;
        &:nth-child(2n+0) {
          margin-left: 0; } } }
    @media (min-width: 1200px) and (max-width: 1479px) {
      width: 31.786666666666665%;
      margin-right: 2.32%;
      &:nth-child(3n+0) {
        margin-right: 0; }
      @include rtl {
        margin-right: 0;
        margin-left: 2.32%;
        &:nth-child(3n+0) {
          margin-left: 0; } } }
    @media (min-width: 1480px) {
      width: 23.9436625%;
      margin-right: 1.40845%;
      &:nth-child(4n+0) {
        margin-right: 0; }
      @include rtl {
        margin-right: 0;
        margin-left: 1.40845%;
        &:nth-child(4n+0) {
          margin-left: 0; } } } } }

.car-card {
  display: block;
  max-width: 340px;
  color: $textColor;
  background-color: $white;
  border: 1px solid $borderColor;
  box-shadow: $cardShadow;
  transition: $mainAnimationSpeed;
  &:hover, &:focus {
    text-decoration: none;
    color: $accent2; }
  &:hover {
    box-shadow: $cardShadowHover; } }


.car-card__img {
  background: $emptyColor;
  padding-bottom: 70.5%;
  @include coverImg(true, true); }

.car-card__content {
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  div {
    display: flex;
    flex-direction: column;
    &:nth-child(1) {
      padding-top: 3px;
      margin-right: 10px;
      @include rtl {
        margin-right: 0;
        margin-left: 10px; }
      span {
        font-weight: 500;
        &:nth-child(1) {
          margin-bottom: 5px; } } }
    &:nth-child(2) {
      span {
        text-align: right;
        @include rtl {
          text-align: left; }
        &:nth-child(1) {
          font-weight: 700;
          font-size: $fz24px;
          color: $accent2;
          white-space: nowrap; }
        &:nth-child(2) {
          font-weight: 500;
          font-size: $fz13px;
          color: $textColor2; } } }
    span {
      line-height: 1.2; } } }

.car-card--small {
  .car-card__content {
    padding: 10px 8px;
    div {
      &:nth-child(1) {
        span {
          &:nth-child(1) {
            margin-bottom: 2px; } } }
      &:nth-child(2) {
        span {
          &:nth-child(1) {
            font-size: $fz20px; } } } } } }

.banner1 {
  height: 450px;
  position: relative;
  background: $emptyColor url(../img/banners/banner1/bg.jpg) center no-repeat;
  background-size: cover;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(#131313, 0.5); }
  > * {
    position: relative;
    z-index: 1; } }

.banner1__container {
  height: 100%; }

.banner1__content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  height: 100%;
  padding: 90px 0; }

.banner1__title {
  margin-bottom: 45px;
  font-family: 'BebasNeueRegular';
  font-size: $fz48px;
  line-height: 1;
  text-transform: uppercase;
  text-align: right;
  color: $textColor3;
  @media (min-width: 992px) {
    font-size: $fz72px; } }

.banner1-btn {
  display: inline-block;
  min-width: 200px;
  padding: 13px 30px;
  font-size: $fz16px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center !important;
  color: $bgColor;
  background: transparent;
  border: 1px solid $bgColor;
  user-select: none;
  &:hover, &:focus {
    text-decoration: none;
    color: $textColor;
    background: $bgColor; } }


.car-brands {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @media (max-width: 991px) {
    justify-content: space-around; }
  > * {
    margin-bottom: 20px;
    @media (max-width: 575px) {
      max-width: none; } } }

.car-brand {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 265px;
  height: 200px;
  padding: 15px;
  background-color: $white;
  border: 1px solid $borderColor; }

a.car-brand {
  color: $textColor;
  transition: $mainAnimationSpeed;
  &:hover, &:focus {
    text-decoration: none;
    color: $textColor;
    border-color: #c2d7fb;
    box-shadow: $cardShadowHover; } }

.car-brand__img {
  position: relative;
  width: 100%;
  min-width: 115px;
  max-width: 115px;
  // height: auto
  margin-right: 15px;
  padding-bottom: 49.358%;
  // background: $emptyColor
  @include rtl {
    margin-right: 0;
    margin-left: 15px; }
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 100%;
    max-height: 100%; } }

.car-brand__content {
  width: 100%;
  span {
    display: block;
    line-height: 1.2;
    &:nth-child(1) {
      font-size: $fz17px;
      font-weight: 500;
      margin-bottom: 8px; }
    &:nth-child(2) {
      font-size: $fz14px; } } }


.banner2 {
  background: $white;
  overflow: hidden;
  @media (min-width: 1900px) {
    background: $white url(../img/banners/banner2/bg.jpg) center right no-repeat;
    background-size: contain; } }

.banner2__content {
  position: relative;
  padding: 60px 30px 40px 30px;
  @media (min-width: 440px) {
    padding: 90px 30px 70px 30px; }
  @media (min-width: 1200px) {
    height: 492px;
    padding: 90px 30px 70px 30px; }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 1100px;
    width: 497px;
    height: 492px;
    background: url(../img/banners/banner2/bg.jpg); }
  @media (min-width: 1480px) {
    padding: 90px 250px 70px 30px;
    &::after {
      left: 1155px; } }
  @media (min-width: 1900px) {
    &::after {
      display: none; } } }

.banner2__title {
  margin-bottom: 75px;
  font-size: $fz40px;
  line-height: 1.5;
  text-transform: uppercase;
  color: $accent3;
  @media (max-width: 767px) {
    margin-bottom: 50px;
    text-align: center; }
  @media (max-width: 439px) {
    font-size: 7.7vw; }
  @media (min-width: 768px) {
    font-size: $fz60px; }
  > span {
    display: inline-block;
    padding: 5px 20px 10px;
    background: $accent2;
    color: $textColor3;
    > span {
      color: $accent3;
      text-transform: lowercase; } } }

.banner2__items {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @media (max-width: 767px) {
    justify-content: center; } }

.banner2__item {
  position: relative;
  min-width: 240px;
  margin-bottom: 30px;
  > span {
    display: block;
    &:nth-child(1) {
      padding-bottom: 15px;
      margin-bottom: 15px;
      font-size: $fz26px;
      font-weight: 500;
      border-bottom: 2px solid $textColor;
      > span {
        font-size: $fz40px;
        font-weight: 700; } }
    &:nth-child(2) {
      font-size: $fz22px;
      font-weight: 300; } }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-40%);
    background-size: cover;
    opacity: 0.15; }

  &:nth-child(1) {
    &::after {
      width: 105px;
      height: 105px;
      background-image: url(../img/banners/banner2/1.svg); } }
  &:nth-child(2) {
    &::after {
      width: 124px;
      height: 124px;
      background-image: url(../img/banners/banner2/2.svg); } }
  &:nth-child(3) {
    &::after {
      width: 101px;
      height: 101px;
      background-image: url(../img/banners/banner2/3.svg); } }
  &:nth-child(4) {
    &::after {
      width: 103px;
      height: 103px;
      background-image: url(../img/banners/banner2/4.svg); } } }


.banner3 {
  position: relative;
  min-height: 450px;
  color: $textColor3;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $emptyColor url(../img/banners/banner3/bg.jpg) center no-repeat;
    background-size: cover;
    @include rtl {
      transform: scaleX(-1); } }
  @media (max-width: 767px) {
    &::before {
      background-position: 70% center; } } }

.banner3__content {
  position: relative;
  z-index: 1;
  padding: 70px 0;
  @media (min-width: 768px) {
    padding: 70px 80px; } }

.banner3__title {
  margin-bottom: 40px;
  font-size: $fz20px;
  line-height: 1.1;
  font-weight: 900;
  font-style: italic;
  text-transform: uppercase;
  @media (min-width: 360px) {
    font-size: $fz24px; }
  @media (min-width: 992px) {
    font-size: $fz32px; }
  @media (min-width: 1200px) {
    font-size: $fz40px; }
  > span {
    display: inline-block;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      transform: skewX(150deg);
      opacity: 0.4;
      box-shadow: inset 0 0 0 1000px black, 0 0 0 8px black, -15px 0 0 8px black, 15px 0 0 8px black;
      @include rtl {
        transform: skewX(-150deg); } }
    > span {
      position: relative;
      z-index: 1; } } }

.banner3__link {
  font-size: $fz16px;
  text-transform: uppercase; }


.banner4 {
  position: relative;
  min-height: 450px;
  padding: 70px 0;
  color: $textColor3;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
