.banner4__title {
  position: relative;
  z-index: 1;
  margin-bottom: 50px;
  font-size: $fz40px;
  font-weight: 300;
  text-transform: uppercase;
  line-height: 1.1; }

.banner4-btn {
  position: relative;
  z-index: 1;
  display: inline-block;
  min-width: 200px;
  padding: 13px 30px;
  font-size: $fz16px;
  font-weight: 300;
  text-align: center;
  color: $bgColor;
  background: transparent;
  border: 1px solid $bgColor;
  user-select: none;
