.banner5__title {
  margin-bottom: 40px;
  font-size: $fz24px;
  line-height: 1.31;
  font-weight: 700;
  color: $accent3;
  @media (min-width: 576px) {
    margin-bottom: 60px;
    font-size: $fz32px; } }

.banner5__btn {
  @media (min-width: 992px) {
    min-width: 300px; } }


.info-block {
  padding: 60px 0;
  overflow: hidden;
  @media (min-width: 992px) {
    padding: 120px 0 60px; } }

.info-block__content {
  float: right;
  @include rtl {
    float: left; }
  @media (min-width: 992px) {
    position: relative;
    max-width: 50%;
    min-height: 420px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      width: 1000px;
      height: 395px;
      background: url(../img/info-bg.png);
      @include rtl {
        right: auto;
        left: 100%;
        transform: scaleX(-1); } } } }

.info-block__title {
  margin-bottom: 20px;
  font-size: $fz26px;
  line-height: 1.2;
  font-weight: 700; }

.faq-section {
  counter-reset: faq-list; }

.faq-section--pr {
  @media (min-width: 1200px) {
    padding-right: 120px;
    @include rtl {
      padding-right: 0;
      padding-left: 120px; } } }

.faq-section--dual {
  @media (min-width: 1200px) {
    display: flex;
    > * {
      width: 100%;
      &:first-child {
        margin-right: 7%;
        @include rtl {
          margin-right: 0;
          margin-left: 7%; } } } } }

.faq-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  li {
    padding: 15px 0;
    border-bottom: 1px solid $borderColor;
    break-inside: avoid;
    > a {
      position: relative;
      display: block;
      padding: 4px 50px 4px 0;
      font-size: $fz16px;
      line-height: 1.25;
      font-weight: 500;
      color: $textColor;
      // background: rgba(red, 0.2)
      user-select: none;
      @include rtl {
        padding: 4px 0 4px 50px; }
      @media (min-width: 768px) {
        padding: 4px 50px 4px 15px;
        @include rtl {
          padding: 4px 15px 4px 50px; } }
      &:hover, &:focus {
        text-decoration: none; }
      &::before {
        counter-increment: faq-list;
        content: counter(faq-list) ". ";
        font-size: $fz18px;
        font-weight: 700;
        color: $accent2; }

      i {
        position: absolute;
        top: 0;
        right: 0;
        width: 30px;
        height: 30px;
        border: 2px solid $textColor2;
        border-radius: 50%;
        @include rtl {
          right: auto;
          left: 0; }
        &::before, &::after {
          content: '';
          position: absolute;
          width: 14px;
          height: 2px;
          background: $textColor2; }
        &::before {
          top: 12px;
          left: 6px;
          @include rtl {
            left: auto;
            right: 6px; } }
        &::after {
          top: 12px;
          left: 6px;
          transform: rotate(90deg);
          @include rtl {
            left: auto;
            right: 6px; } } }

      &:hover {
        i {
          background: $accent2;
          border-color: $accent2;
          &::before, &::after {
            background: $white; } } }
      &:active {
        i {
          opacity: 0.5; } }

      &.active {
        i {
          background: $accent2;
          border-color: $accent2;
          &::before {
            background: $white; }
          &::after {
            display: none; } }
        &:hover {
          i {
            opacity: 0.75; } }
        &:active {
          i {
            opacity: 0.5; } }
        & + span {
          display: block; } } }

    > span {
      display: none;
