// vertical align mixin
@mixin vertical($elem) {
  position: relative;
  letter-spacing: -0.5em;
  & > * {
    letter-spacing: normal; }
  &::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle; }
  >#{$elem} {
    display: inline-block;
    vertical-align: middle; } }

// Hover transitions
@mixin hover($opacity: .8, $duration: 0.15s) {
  transition-property: all;
  transition-duration: $duration;
  transition-timing-function: ease;
  &:hover, &:focus {
    // transition-duration: 0s
    opacity: $opacity; } }

@mixin trns($duration: 0.15s, $timing: ease) {
  transition-property: all;
  transition-duration: $duration;
  transition-timing-function: $timing;
  // &:hover, &:focus
 }  //// transition-duration: 0s

// Text overflow
@mixin text-overflow($posa: false) {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
  @if $posa {
    max-width: 100%; } } // If position absolute

// Multiline text overflow (only webkit) (don't forget to set height !)
@mixin multi-overflow($lines: 2) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis; }

// Default setting for custom icon packs from http://fontello.com
@mixin iconpack {
  font-family: "custom-icon-pack";
  font-style: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

// Justify menu (Only <ul>)
@mixin navjust {
  margin: 0 auto;
  display: table;
  table-layout: auto;
  float: none;
  width: 100%;
  li {
    display: table-cell;
    float: none;
    text-align: center; }
  // @media (max-width: 767px)
  //// li
 }  //  // display: block

// https://css-tricks.com/snippets/css/clear-fix/
@mixin clearfix {
  &::after {
    content: '';
    display: table;
    clear: both; } }

// IE 11 align-items:center и min-height.
// Просто добавь height меньший чем min-height
// (https://github.com/philipwalton/flexbugs/issues/231) - тут есть и алтернативное решение с псевдоэлементом (сделал миксин +flex-ie11-mih)
@mixin flex-ie11-mih {
  &::after {
    content: '';
    display: block;
    min-height: inherit;
    font-size: 0; } }

// Center img tag like background-size: cover
@mixin coverImg($limitWidth: false, $limitHeight: false) {
  position: relative;
  overflow: hidden;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    @if $limitWidth {
      max-width: 100%; }
    @if $limitHeight {
      max-height: 100%; } } }

@mixin stretchImg {
  img {
    width: 100%;
    height: 100%; } }

// for ie10 - edge18
@mixin ie10-18Only {
  @media (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content; } }

// hide checkbox or radiobox, but don't turn off the focus feature
@mixin hideCheckOrRadioButFocus {
  position: absolute;
  opacity: 0;
  // width: 0 // do not work since Chrome 83 update
  // height: 0 // do not work since Chrome 83 update
  pointer-events: none; }

// If RTL in html tag
@mixin rtl {
  @at-root html[dir="rtl"] & {
    @content; } }
