// _____ VARIABLES _____ //

// Generic
// ==========================================================================

// $inputPlaceholder: #666
// $inputRequireValid: #666
// $inputRequireInvalid: #666

$defaultFontFamily: 'PFDinTextUniversal', sans-serif;

// use REM (ie11+) !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
$baseFontSize: 15px;
$baseLineHeight: 1.467;

$fz11px: #{11px/$baseFontSize}rem;
$fz12px: #{12px/$baseFontSize}rem;
$fz13px: #{13px/$baseFontSize}rem;
$fz14px: #{14px/$baseFontSize}rem;
$fz15px: #{15px/$baseFontSize}rem;
$fz16px: #{16px/$baseFontSize}rem;
$fz17px: #{17px/$baseFontSize}rem;
$fz18px: #{18px/$baseFontSize}rem;
$fz20px: #{20px/$baseFontSize}rem;
$fz22px: #{22px/$baseFontSize}rem;
$fz24px: #{24px/$baseFontSize}rem;
$fz26px: #{26px/$baseFontSize}rem;
$fz28px: #{28px/$baseFontSize}rem;
$fz32px: #{32px/$baseFontSize}rem;
$fz40px: #{40px/$baseFontSize}rem;
$fz48px: #{48px/$baseFontSize}rem;
$fz60px: #{60px/$baseFontSize}rem;
$fz72px: #{72px/$baseFontSize}rem;


// Colors
// ==========================================================================

$bgColor: #f2f2f2;

$white: white;

$accent: #0d4094;
$accent2: #124eb3;
$accent3: #007aff;
$accent4: #ffa502;
$accent5: #41af99;
$lightGray: #d3d3d3;
$emptyColor: #b0b5bb;

$textColor: #282a2e;
$textColor2: #828c98;
$textColor3: white;

$link: $accent2;
$linkHover: rgba($accent2, 0.75);
$linkActive: rgba($accent2, 0.5);

$inputPlaceholder: #b8b8b8;

$borderColor: $lightGray;
$borderColor2: #e8e8e8;
$borderColorFocus: darken($borderColor, 15%);

$cardShadow: 0 3px 7px rgba(0, 0, 0, 0.05);
$cardShadowHover: 0 3px 12px rgba(0, 0, 0, 0.15);

// Animation

$mainAnimationSpeed: 0.15s;
