/* Modals */

$green: green;

.mfp-bg {
  background: $accent;
  opacity: 0.7; }

.mfp-container {
  padding: 0;
  text-align: center !important; }

.mfp-iframe-holder {
  padding: 50px 0; }

.mfp-iframe-holder .mfp-content {
  max-width: 1400px; }

.mfp-close {
  font-size: 40px; }

.mfp-iframe-holder .mfp-close {
  top: -50px; }

.mfp-image-holder .mfp-close, .mfp-iframe-holder .mfp-close {
  @include rtl {
    right: auto;
    left: 0;
    padding-right: 0;
    padding-left: 6px;
    text-align: left !important; } }

.modal {
  width: 100%;
  max-width: 460px;
  margin: 0;
  background-color: $white;
  @media (max-width: 509px) {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow-y: auto;
    overscroll-behavior-y: contain; }
  @media (min-width: 510px) {
    margin: 50px auto; } }

.modal--change-dates {
  max-width: 750px;
  .modal__form {
    padding: 20px 20px 30px;
    @media (min-width: 768px) {
      padding: 60px 115px 65px; } }
  .modal__title {
    margin-bottom: 50px;
    font-size: $fz18px; } }

.modal--gray {
  background: $bgColor; }

.modal--wide-lg {
  max-width: 860px; }

.modal__inner {
  position: relative; }

.modal__close {
  position: absolute;
  z-index: 2;
  top: 63px;
  right: 8px;
  @include rtl {
    right: auto;
    left: 8px; }
  &::before, &::after {
    left: 4px;
    right: 4px; }
  @media (min-width: 510px) {
    right: -30px;
    top: -28px;
    @include rtl {
      right: auto;
      left: -30px; } } }

.modal__close--inside {
  top: 5px;
  right: 5px;
  @include rtl {
    right: auto;
    left: 5px; } }

.modal__user-actions {
  display: flex;
  font-size: $fz22px;
  line-height: 1;
  > * {
    flex: 1;
    padding: 17.5px 10px;
    text-align: center !important;
    border: 1px solid transparent; }
  a {
    color: $textColor2;
    background-color: #e8f0fd;
    border-color: #d2e2fc;
    &:hover, &:focus {
      text-decoration: none; } }
 }  // span

.modal__title {
  margin-bottom: 30px;
  font-size: $fz22px;
  line-height: 1;
  font-weight: 700;
  text-align: center; }

.modal__text {
  margin-bottom: 30px;
  font-size: $fz14px;
  line-height: 1.3;
  color: $textColor2; }

.modal__form {
  width: 100%;
  padding: 20px 20px 30px;
  @media (min-width: 510px) {
    padding: 20px 40px 30px; } }

.modal__text-divider {
  padding: 15px 0;
  font-size: $fz14px;
  line-height: 1.1;
  text-align: center;
  color: $textColor2; }

.modal__social-login {
  display: flex;
  > * {
    flex: 1;
    &:not(:last-child) {
      margin-right: 10px;
      @include rtl {
        margin-right: 0;
        margin-left: 10px; } } } }

.modal__social-btn {
  padding: 11px 0;
  text-align: center !important;
  background: $emptyColor;
  &:hover, &:focus {
    background: lighten($emptyColor, 3%); }
  svg {
    width: 20px;
    height: 20px; } }

.modal__social-btn--fb {
  background: #2f55a4;
  &:hover, &:focus {
    background: #6380bb; } }
.modal__social-btn--tw {
  background: #1da1f2;
  &:hover, &:focus {
    background: #56b9f5; } }
.modal__social-btn--gg {
  background: #d44638;
  &:hover, &:focus {
    background: #df746a; } }


.modal__both-sides-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  label {
    font-size: $fz14px;
    color: $textColor2; } }

.modal__link {
  font-size: $fz14px;
  line-height: 1.1;
  color: $textColor2;
  &:hover, &:focus {
    color: $textColor2; } }

.modal__bottom-text {
  font-size: $fz14px;
  text-align: center;
  a {
    text-decoration: underline; } }


.modal__checkbox-group {
  margin-top: 25px;
  > * {
    margin-bottom: 20px; } }

.modal__radio-group {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
  > * {
    margin-bottom: 5px;
    &:not(:last-child) {
      margin-right: 30px;
      @include rtl {
        margin-right: 0;
        margin-left: 30px; } } } }


.modal__btn-group {
  margin-top: 40px;
  margin-bottom: 20px; }

.modal__form--gratitude {
  padding: 50px 20px 50px;
  @media (min-width: 510px) {
    padding: 90px 30px 80px; }
  @media (min-width: 768px) {
    padding: 90px 60px 80px; } }

.modal__gratitude-text {
  position: relative;
  font-size: $fz20px;
  line-height: 1.3;
  @media (max-width: 509px) {
    padding-top: 88px;
    text-align: center !important; }
  @media (min-width: 510px) {
    padding-left: 88px;
    font-size: 1.6rem;
    @include rtl {
      padding-left: 0;
      padding-right: 88px; } }
  @media (min-width: 768px) {
    font-size: $fz28px; }
  svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 65px;
    height: 65px;
    @include rtl {
      left: auto;
      right: 0; }
    @media (max-width: 509px) {
      left: 50%;
      transform: translateX(-50%);
      @include rtl {
        left: auto;
        right: 50%;
        transform: translateX(50%); } }
    @media (min-width: 510px) {
      top: 50%;
      transform: translateY(-50%); } } }


.modal__dates {
  position: relative;
  @media (max-width: 991px) {
    margin-bottom: 40px; } }

.modal__date {
  > div {
    &:nth-child(1) {
      font-size: $fz16px; } }
  @media (max-width: 575px) {
    &:nth-child(1) {
      > div {
        &:nth-child(2) {
          > div {
            &:nth-child(2) {
              > div {
                &:nth-child(1) {
                  margin-bottom: 15px; } } } } } } } }
  @media (max-width: 991px) {
    &:nth-child(1) {
      margin-bottom: 10px;
      > div {
        &:nth-child(2) {}
        > div {
          &:nth-child(1) {
            margin-bottom: 15px; } } } }
    > div {
      &:nth-child(1) {
        margin-bottom: 5px; } } }
  @media (min-width: 992px) {
    display: flex;
    &:nth-child(1) {
      margin-bottom: 40px;
      > div {
        &:nth-child(2) {}
        > div {
          &:nth-child(1) {
            margin-bottom: 25px; } } } }
    > div {
      &:nth-child(1) {
        min-width: 100px;
        max-width: 100px;
        padding-right: 15px;
        padding-top: 12px;
        text-align: right;
        @include rtl {
          padding-right: 0;
          padding-left: 15px;
          text-align: left !important; } }
      &:nth-child(2) {
        width: 100%; } } } }

.modal__date-2col {
  @media (min-width: 576px) {
    display: flex;
    > * {
      &:nth-child(1) {
        width: 100%;
        margin-right: 20px;
        @include rtl {
          margin-right: 0;
          margin-left: 20px; } }
      &:nth-child(2) {
        min-width: 120px; } } } }

.modal__date-btn-group {
  @media (min-width: 992px) {
    margin-top: 65px;
    padding-left: 100px;
    @include rtl {
      padding-left: 0;
      padding-right: 100px; } } }
