/* Fonts */
// Don't forget - font-display: swap

$fontPath: '../fonts/PFDinTextUniversal/';

@font-face {
  font-display: swap;
  font-family: 'PFDinTextUniversal';
  src: url($fontPath + 'PFDinTextUniversal-Light.woff2') format("woff2"), url($fontPath + 'PFDinTextUniversal-Light.woff') format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-display: swap;
  font-family: 'PFDinTextUniversal';
  src: url($fontPath + 'PFDinTextUniversal.woff2') format("woff2"), url($fontPath + 'PFDinTextUniversal.woff') format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-display: swap;
  font-family: 'PFDinTextUniversal';
  src: url($fontPath + 'PFDinTextUniversal-Medium.woff2') format("woff2"), url($fontPath + 'PFDinTextUniversal-Medium.woff') format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-display: swap;
  font-family: 'PFDinTextUniversal';
  src: url($fontPath + 'PFDinTextUniversal-Bold.woff2') format("woff2"), url($fontPath + 'PFDinTextUniversal-Bold.woff') format("woff");
  font-weight: bold;
  font-style: normal; }


$fontPath: '../fonts/BebasNeue/';
@font-face {
  font-display: swap;
  font-family: 'BebasNeueRegular';
  src: url($fontPath + 'BebasNeueRegular.woff2') format("woff2"), url($fontPath + 'BebasNeueRegular.woff') format("woff");
  font-weight: normal;
  font-style: normal; }
