/* Footer */

.footer {
  /* Fixed footer */
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  /* Fixed footer END */
  color: $textColor3;
  background: #1d232b;
  overflow: hidden;
  @media (max-width: 991px) {
    padding-bottom: 50px; }

  ul {
    margin: 0 0 30px;
    padding: 0;
    list-style-type: none;
    line-height: 1.2;
    @media (min-width: 992px) {
 }      // font-size: $fz15px
    li {
      &:not(:last-child) {
        margin-bottom: 10px; }
      a {
        color: $textColor3; } } } }

.footer__container {
  @media (min-width: 992px) {
    display: flex; } }

.footer__side-1, .footer__side-2 {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px; }

.footer__side-1 {
  padding-top: 35px;
  @media (min-width: 992px) {
    padding-right: 60px;
    width: 100%;
    @include rtl {
      padding-right: 0;
      padding-left: 60px; } }
  @media (min-width: 1200px) {
    padding-right: 130px;
    @include rtl {
      padding-right: 0;
      padding-left: 130px; } } }

.footer__side-2 {
  position: relative;
  background: #2e373f;
  padding-top: 40px;
  > * {
    position: relative;
    z-index: 1; }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: -5000px;
    bottom: 0;
    left: -5000px;
    background: #2e373f; }
  @media (min-width: 992px) {
    padding-top: 109px;
    padding-left: 60px;
    min-width: 360px;
    @include rtl {
      padding-left: 0;
      padding-right: 60px; }
    &::after {
      left: 0;
      @include rtl {
        left: -5000px;
        right: 0; } } }
  @media (min-width: 1200px) {
    padding-left: 120px;
    min-width: 460px;
    @include rtl {
      padding-left: 0;
      padding-right: 120px; } } }

.footer__logo {
  display: inline-block;
  margin-bottom: 40px;
  align-self: flex-start; }

.footer__lists {
  margin-bottom: 20px;
  @media (min-width: 992px) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px; } }

.footer__social {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
  > * {
    &:not(:last-child) {
      margin-right: 20px;
      @include rtl {
        margin-right: 0;
        margin-left: 20px; } } }
  @media (min-width: 992px) {
    margin-bottom: 50px; } }

.footer__copy {
  color: #c0c0c0;
  font-size: $fz12px;
  @media (max-width: 991px) {
    position: absolute;
    bottom: 15px; } }

.footer__payments {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
  > * {
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 10px;
      @include rtl {
        margin-right: 0;
        margin-left: 10px; } } }
  @media (min-width: 992px) {
    margin-top: auto;
    margin-bottom: 50px;
    > * {
      &:not(:last-child) {
        margin-right: 20px;
        @include rtl {
          margin-right: 0;
          margin-left: 20px; } } } } }
